import request from "@/utils/request";

const helperUrl =
  "https://www.fastmock.site/mock/c25e396c1d2ef07e8f1fc0acf0adc1a5/vue-helper";
const toolsUrl =
  "https://www.fastmock.site/mock/f10cad4615c38b76aa9ee7e5030157e5/vue-tools";

/*               富 文 本 框 页 面 的 接 口               */

// 云南的 /examAnswer/safetyCommitment 安全承诺里面的富文本框的案例
export function safetyCommitmentConfig() {
  return request({
    url: helperUrl + "/api/facility-base/safety-commitment-config",
    method: "get",
  });
}

// 富文本框 详情
export const richTextDetail = (id) => {
  return request({
    url: helperUrl + "/api/facility-base/safety-commitment-config/" + id,
    method: "get",
  });
};

/*               富 文 本 框 页 面 的 接 口               */

