/*               前 端 实 现 表 格 分 页 的 接 口               */

/*               高 德 地 图 获 取 天 气 的 接 口               */
export function gaoDeWeather(city) {
  return request({
    url: "https://restapi.amap.com/v3/weather/weatherInfo",
    method: "get",
    params: {
      key: "e3d4cbac03e5c2a465ab04b682ca005f", // 请求服务权限标识
      city: city, // 城市编码
      extensions: null, // 气象类型 可选值：base/all。base:返回实况天气，all:返回预报天气
      output: null, // 返回格式 (可选值：JSON,XML)
    },
  });
}
/*               高 德 地 图 获 取 天 气 的 接 口               */

export function getAreaCount() {
  return request({
    url: toolsUrl + "/getAreaCount",
    method: "get",
  });
}
