<!--
 * @Description: 将网页内容导出成图片
 * @Author: mhf
 * @Date: 2023-04-11 00:17:29
-->
<template>
  <div>
    <backHomeBtn>
      <template>
        <div class="div">
          <el-button type="primary" @click="onExport" size="small"
            >导出jpg</el-button
          >

          <div class="imageWrapper" ref="imageWrapper">
            <el-table :data="controlsRecord" style="width: 100%">
              <el-table-column
                prop="controlsId"
                label="controlsId"
              ></el-table-column>
              <el-table-column
                prop="controlsType"
                label="controlsType"
              ></el-table-column>
              <el-table-column prop="id" label="id"></el-table-column>
              <el-table-column prop="title" label="title"></el-table-column>
            </el-table>
            <div style="margin: 30px 0">table</div>
            <el-table :data="safetyMeasureRecord" style="width: 100%">
              <el-table-column prop="id" label="annex"></el-table-column>
              <el-table-column prop="content" label="content"></el-table-column>
              <el-table-column
                prop="passFlag"
                label="passFlag"
              ></el-table-column>
              <el-table-column
                prop="safetyMeasureId"
                label="safetyMeasureId"
              ></el-table-column>
            </el-table>

            <div class="img-box">
              <el-image
                style="width: 100px; height: 100px"
                :src="imgUrl"
                :preview-src-list="[imgUrl]"
              >
              </el-image>
              <img
                style="width: 100px; height: 100px"
                src="../../../public/img/headPic.jpg"
                alt=""
              />
              <el-image
                v-for="(item, index) in imageList"
                :key="index"
                style="width: 100px; height: 100px; margin-left: 6px"
                :src="item"
                :preview-src-list="[item]"
              >
              </el-image>
            </div>
          </div>
        </div>
      </template>
    </backHomeBtn>
  </div>
</template>

<script>
import { exportPdf } from "@/api/allRequset";
import html2canvas from "html2canvas";

export default {
  name: "exportPdf",
  components: {},
  props: {},
  computed: {},
  filters: {},
  watch: {},
  data() {
    return {
      controlsRecord: [],
      safetyMeasureRecord: [],
      imgUrl:
        "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
      imageList: [],
    };
  },
  methods: {
    /**
     * @Interface 接口
     * @description: 调接口获取数据
     * */
    exportPdf() {
      exportPdf().then((res) => {
        this.imageList = res.data.data.imgUrl;
        this.controlsRecord = res.data.data.controlsRecord;
        this.safetyMeasureRecord = res.data.data.safetyMeasureRecord;
      });
    },

    /**
     * @Event 方法
     * @description: 导出自定义div段
     * */
    onExport() {
      const clientWidth = this.$refs.imageWrapper.offsetWidth;
      const clientHeight = this.$refs.imageWrapper.offsetHeight;
      const kh = [clientWidth, clientHeight];

      html2canvas(this.$refs.imageWrapper, {
        useCORS: true,
        logging: true,
      }).then((canvas) => {
        const dataURL = canvas.toDataURL("image/png", 1.0);
        this.download(dataURL, kh);
      });
    },

    /**
     * @Event 方法
     * @description: 自定义下载方法
     * */
    download(imgUrl, kh) {
      this.getUrlBase64(imgUrl, kh).then((base64) => {
        const link = document.createElement("a");
        link.href = base64;
        // link.download = `${this.row.type} - ${this.formData.applyStaffName} -  ${this.formData.workOrder}.jpg`;
        link.download = `exportToJpg.jpg`; // 自定义导出的文件命名
        link.click();
      });
    },

    getUrlBase64(url, kh) {
      return new Promise((resolve) => {
        let canvas = document.createElement("canvas");
        const ctx = canvas.getContext("2d");
        const img = new Image();
        img.crossOrigin = "*"; // 允许跨域
        img.src = url;
        img.onload = () => {
          // eslint-disable-next-line prefer-destructuring
          canvas.height = kh[1];
          // eslint-disable-next-line prefer-destructuring
          canvas.width = kh[0];
          ctx.drawImage(img, 0, 0, kh[0], kh[1]);
          const dataURL = canvas.toDataURL("image/png");
          canvas = null;
          resolve(dataURL);
        };
      });
    },
  },
  created() {
    this.exportPdf();
  },
  mounted() {},
};
</script>

<style lang="scss" scoped></style>
